import React, { Component } from "react";

export class Text extends Component {
  // Use to add a text (tp-text)
  constructor(props) {
    super();
    this.state = {
      id: props.id,
      value: props.value,
      content: "",
    };
  }

  text = (props) => {
    // Generate a text
    props.id = props.id || false;

    props.value.forEach((t) => {
      props.content += `${t}\n`;
    });

    return (
      <p className="tp-text" id={props.id}>
        {props.content}
        <br />
      </p>
    );
  };

  render() {
    return <React.StrictMode>{this.text(this.state)}</React.StrictMode>;
  }
}
